import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.div`
  margin-bottom: 35px;
`;

const SubTitle = styled.h2`
  font-weight: normal;
  font-size: 32px;
`;

const Form = styled.form`
  max-width: 500px;
  margin: 0 auto;
`;

const InputDiv = styled.div`
  position: relative;
  margin-bottom: 10px;
  input {
    width: 100%;
    padding: 8px 8px 8px 28%;
    border: 1px solid #e9e8e7;
    outline: 0;
  }
`;

const InputLabel = styled.label`
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  padding: 8px;
  border-right: 1px solid #e9e8e7;
  width: 25%;
  text-align: center;
  color: rgba()
`;

class LoginAndRegister extends Component {
  render() {
    return (
      <Div>
        <SubTitle>一、註冊行為</SubTitle>
        <Form>
          <InputDiv>
            <InputLabel>帳號</InputLabel>
            <input type="text" />
          </InputDiv>
          <InputDiv>
            <InputLabel>密碼</InputLabel>
            <input type="password" />
          </InputDiv>
          <button>登入</button>
        </Form>
      </Div>
    );
  }
}

export default LoginAndRegister;
